/* ================================= 
  Element Styles
==================================== */

* {
    box-sizing: border-box;
  }
  
  body {
    font: 1em/1.5 'Roboto', sans-serif;
    color: #4c4c4c;
    background: #f6f7f7;
    padding-bottom: 3em;
    margin: 0;
  }
  
  a {
    text-decoration: none;
  }
  
  ul,
  li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
 input {
    font-size: 1.12em;
    height: 46px;
    padding-bottom: 1em;
    padding-left: 0;
    background: none;
    border: none;
    outline: 0;
    border-bottom: 4px solid #e4e4e4;
    transition: border-color .4s;
  }
  
input:focus {
    border-color: #c1c1c1;
  }
  
  .search-button {
    width: 40px;
    height: 35px;
    border: 0;
    background: none;
    cursor: pointer;
    outline: 0;
    position: relative;
    z-index: 2000;
  }
  
  /* ================================= 
    Page Components
  ==================================== */
  
  .main-title {
    font-size: 1.4em;
    line-height: 80px;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin: 0;
  }
  
  .search-form input:only-of-type {
    width: 80%;
    margin-right: -40px;
    padding-right: 35px;
  }
  
  .search-button {
    float: right;
    margin-right: 10px;
  }
  
  .icn-search {
    font-weight: 700;
  }
  
  .icon-img {
    font-size: 6em;
    margin-bottom: 0;
    color: #7bcbc4;
    display: block;
  }
  
  .no-imgs  {
    color: #7bcbc4;
    height: 140px;
    padding-top: 62px;
    text-align: center;
    font-size: 1.25em;
  }
  
  .img-list a {
    display: block;
    margin: auto;
  }
  
  .img-wrap {
    background-color: #fff;
    margin-bottom: .8em;
    position: relative;
  }
  
  .img-wrap img {
    min-width: 100%;
  }
  
  /* ================================= 
    Layout Styles
  ==================================== */
  
  .inner,
  .main-content {
    width: 100%;
    max-width: 1025px;
    margin: auto;
  }
  
  .main-header {
    /* position: relative; */
    top: 0;
    left: 0;
    width: 100%;
    background: #fff;
    /* min-height: 80px; */
    z-index: 1000;
  }
  
  .main-content::after {
    content: " ";
    display: table;
    clear: both;
  }
  
 .is-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  
  /* ================================= 
    Media Queries
  ==================================== */
  
@media (min-width: 0) and (max-width: 549px) {
    .img-list li {
      text-align: center;
      margin: 20px 0 0;
      font-size: 0;
    }
  }
  
@media (min-width: 550px) {
    .img-list li {
      float: left;
      width: 50%;
      margin: 0;
    }
    .img-wrap img {
      height: 235px;
    }
    .img-list .no-imgs {
      float: none;
      width: 100%;
    }
    .img-wrap {
      border: 1px solid white;
      padding: 0;
      overflow: hidden;
      font-size: 0;
    }
  }
  
  @media (min-width: 769px) {
    /* body {
      padding-top: 115px;
    } */
    .main-title {
      float: left;
    }
    .search-form {
      width: 422px;
      float: right;
      margin-top: 34px;
    }
    .img-list li {
      width: 33.333333333%;
    }
  }
  
@media (min-width: 769px) { 
    .img-list li:nth-of-type(3n+1) {
      clear: left;
    }
}